body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.ChatSecion {
  overflow: auto;
  grid-column: span 1;
  grid-row: span 1;
  background-color: #6e968c;
  border-radius: 0 0 25px 25px;
  padding-bottom: 20px;
  min-width: 350px;
}


.ChatItem {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  padding: 15px;
  justify-content: flex-start;
}

.ChatUserInputSec {
  display: flex;
  align-items: flex-start;
  grid-gap: 10px;
  gap: 10px;
  margin: 20px;
  justify-content: space-around;
  border-radius: 20px;
  grid-column: span 1;
  grid-row: span 1;
  min-width: 350px;
}

.MainContainer {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 8fr 2fr;
  height: 100vh;
  background-color: #447066;
  transition: margin-left 0.5s;
}

::-webkit-scrollbar {
  width: 10px;
  border-radius: 25px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #cad2c5;
  border-radius: 25px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #2f3e46;
  border-radius: 25px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.SideBar {
  grid-row: span 2;
  justify-self: center;

  background-color: #354f52;
  width: 70%;
  display: flex;
  flex-direction: column;
  


  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
}






.sidebar a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
  transition: 0.3s;
}

.sidebar a:hover {
  color: #f1f1f1;
}

.sidebar .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}

.openbtn {
  font-size: 20px;
  cursor: pointer;
  background-color: #111;
  color: white;
  padding: 10px 15px;
  border: none;
}

.openbtn:hover {
  background-color: #444;
}



/* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */
@media screen and (max-height: 450px) {
  .sidebar {padding-top: 15px;}
  .sidebar a {font-size: 18px;}
}

.HistorySection{
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
    overflow: auto;

}



.HistoryItem{
    padding: 10px;
    background-color: #71938b;
    display: flex;
}
